@import "https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600&display=swap";
html {
  overflow-x: hidden;
}

body {
  color: #859196;
  background: #fff;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  line-height: 1.8;
}

a {
  -o-transition: .3s all ease;
  color: #4586ff;
  transition: all .3s;
}

a:hover {
  color: #4586ff;
  text-decoration: none;
}

h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5 {
  color: #000;
  font-weight: normal;
  line-height: 1.45;
}

.text-primary {
  color: #4586ff !important;
}

.ftco-navbar-light {
  z-index: 3;
  position: absolute;
  left: 0;
  right: 0;
  background: none !important;
}

@media screen and (width <= 991px) {
  .ftco-navbar-light {
    position: relative;
    top: 0;
    background: #000 !important;
  }
}

.ftco-navbar-light .navbar-nav > .nav-item > .nav-link {
  padding: 30px 20px;
  font-size: 16px;
}

@media screen and (width <= 960px) {
  .ftco-navbar-light .navbar-nav > .nav-item > .nav-link {
    padding: 10px 0;
  }
}

.ftco-navbar-light .navbar-nav > .nav-item.ftco-cta a {
  color: #fff;
}

.ftco-navbar-light .navbar-nav > .nav-item.ftco-seperator {
  margin-left: 20px;
  padding-left: 20px;
  position: relative;
}

@media screen and (width <= 960px) {
  .ftco-navbar-light .navbar-nav > .nav-item.ftco-seperator {
    margin-left: 0;
    padding-left: 0;
  }
}

.ftco-navbar-light .navbar-nav > .nav-item.ftco-seperator:before {
  content: "";
  background: #ffffff0d;
  width: 2px;
  position: absolute;
  top: 10px;
  bottom: 10px;
  left: 0;
}

@media screen and (width <= 960px) {
  .ftco-navbar-light .navbar-nav > .nav-item.ftco-seperator:before {
    display: none;
  }
}

.ftco-navbar-light .navbar-toggler {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .1em;
  border: none;
  padding-right: 0;
  font-size: 16px;
  color: #ffffff80 !important;
}

.ftco-navbar-light.scrolled {
  margin-top: -130px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff !important;
}

@media screen and (width <= 768px) {
  .ftco-navbar-light.scrolled .navbar-nav {
    background: none;
    border-radius: 0;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

@media screen and (width <= 576px) {
  .ftco-navbar-light.scrolled .navbar-nav {
    background: none;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.ftco-navbar-light.scrolled .navbar-toggler {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .1em;
  border: none;
  padding-right: 0;
  font-size: 16px;
  color: #00000080 !important;
  border-color: #00000080 !important;
}

.ftco-navbar-light.scrolled .nav-link {
  color: #000 !important;
  padding-top: .7rem !important;
  padding-bottom: .7rem !important;
}

.ftco-navbar-light.scrolled .nav-link.active {
  color: #4586ff !important;
}

.ftco-navbar-light.scrolled.awake {
  -o-transition: .3s all ease-out;
  margin-top: 0;
  transition: all .3s ease-out;
}

.ftco-navbar-light.scrolled.sleep {
  -o-transition: .3s all ease-out;
  transition: all .3s ease-out;
}

.ftco-navbar-light.scrolled .navbar-brand {
  color: #000;
}

.navbar-brand {
  text-transform: uppercase;
  letter-spacing: .1em;
}

.bg-image, .ftco-cover {
  background-repeat: no-repeat;
  background-size: cover;
}

.ftco-cover {
  background-position: center;
}

.ftco-cover .ftco-heading {
  color: #fff;
  font-size: 45px;
  line-height: 1.34;
}

@media screen and (width <= 768px) {
  .ftco-cover .ftco-heading {
    font-size: 37px;
    line-height: 1.3;
  }
}

.ftco-cover .ftco-subheading, .ftco-cover p {
  color: #ffffff80;
}

@media screen and (width <= 768px) {
  .ftco-cover .btn {
    border: 1px solid red;
    width: 100% !important;
  }
}

.ftco-cover a {
  color: #fff;
  position: relative;
}

.ftco-cover a:before {
  content: " ";
  background: #4586ff;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.ftco-cover.overlay {
  position: relative;
}

.ftco-cover.overlay:before {
  content: "";
  background: #0009;
  position: absolute;
  inset: 0;
}

.ftco-cover .ftco-vh-100 {
  min-height: 700px !important;
}

.ftco-slant {
  position: relative;
  overflow: hidden;
}

.ftco-slant:after {
  content: "";
  background: #f2f2f2;
  width: 120%;
  height: 200px;
  position: absolute;
  bottom: -100px;
  left: -10%;
  transform: rotate(-5deg);
}

@media screen and (width <= 960px) {
  .ftco-slant:after {
    bottom: -150px;
  }
}

@media screen and (width <= 768px) {
  .ftco-slant:after {
    bottom: -170px;
  }
}

.ftco-slant.ftco-slant-white:after {
  background: #fff;
}

.ftco-slant.ftco-slant-dark:after {
  background: #263135;
}

.ftco-uppercase {
  letter-spacing: .1em;
  font-size: 30px;
}

.btn {
  letter-spacing: .1em;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 4px;
  padding: 20px 30px;
  font-size: 14px;
  font-weight: bold;
}

.btn.btn-sm {
  padding: 10px 20px;
  font-size: 12px;
}

.btn:before {
  display: none;
}

.btn:hover, .btn:active, .btn:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  outline: none;
}

.btn.btn-primary {
  color: #fff;
  background: #4586ff;
  border-color: #4586ff;
}

.btn.btn-primary:hover, .btn.btn-primary:focus, .btn.btn-primary:active {
  background: #1265ff;
  border-color: #1265ff;
}

.btn.btn-outline-white {
  background: none;
  border-color: #fff3;
}

.btn.btn-outline-white:hover, .btn.btn-outline-white:focus, .btn.btn-outline-white:active {
  color: #000;
  background: #fff;
  border-color: #fff;
}

.form-control {
  border: 1px solid #0003;
  height: 55px;
  font-size: 18px;
  color: #000 !important;
  background: none !important;
}

.form-control:focus, .form-control:active {
  border: 1px solid #000;
}

textarea {
  height: inherit !important;
}

.ftco-vh-100 {
  height: 100vh;
}

@media screen and (width <= 960px) {
  .ftco-vh-100 {
    height: inherit;
    padding-top: 5em;
    padding-bottom: 5em;
  }
}

.ftco-vh-75 {
  height: 75vh;
}

@media screen and (width <= 960px) {
  .ftco-vh-75 {
    height: inherit;
    padding-top: 5em;
    padding-bottom: 5em;
  }
}

.ftco-animate {
  opacity: 0;
  visibility: hidden;
}

.bg-primary {
  background: #4586ff !important;
}

.ftco-section, .ftco-footer {
  padding: 7em 0;
}

.ftco-section.ftco-slant, .ftco-footer.ftco-slant {
  padding: 7em 0 22em;
}

.ftco-section .ftco-heading, .ftco-footer .ftco-heading {
  font-size: 40px;
  line-height: 1.5;
}

@media screen and (width <= 768px) {
  .ftco-section .ftco-heading, .ftco-footer .ftco-heading {
    font-size: 30px;
  }
}

.ftco-section.bg-light, .ftco-footer.bg-light {
  background: #f2f2f2 !important;
}

.ftco-bg-dark {
  background: #263135;
}

.ftco-footer {
  font-size: 16px;
}

.ftco-footer .ftco-footer-logo {
  text-transform: uppercase;
  letter-spacing: .1em;
}

.ftco-footer .ftco-footer-widget h2 {
  font-weight: normal;
}

.ftco-footer p, .ftco-footer a {
  color: #ffffff4d;
}

.ftco-footer a:hover {
  color: #4586ff;
}

.ftco-footer .ftco-heading {
  font-size: 24px;
}

.ftco-footer .ftco-heading-2 {
  letter-spacing: .05em;
  text-transform: uppercase;
  color: #fff9;
  font-size: 14px;
  font-weight: bold;
}

.ftco-pricing-popular {
  position: relative;
}

.ftco-pricing-popular .popular-text {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: .1em;
  background: #4586ff;
  margin-top: -16px;
  padding: 2px 10px;
  font-size: 14px;
  position: absolute;
  right: 0;
  transform: rotate(90deg);
}

.ftco-pricing h2 {
  text-transform: uppercase;
  font-size: 17px;
}

.ftco-pricing .ftco-price-per {
  font-size: 70px;
}

.ftco-pricing .ftco-price-per sup {
  font-size: 20px;
  top: -1.5em;
}

.ftco-pricing .ftco-price-per strong {
  color: #000;
}

.ftco-pricing .ftco-price-per span {
  font-size: 20px;
}

.ftco-pricing ul li {
  margin-bottom: 10px;
}

.ftco-footer-social li {
  margin: 0 10px 10px 0;
  list-style: none;
  display: inline-block;
}

.ftco-footer-social li a {
  float: left;
  background: #ffffff0d;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: block;
  position: relative;
}

.ftco-footer-social li a span {
  font-size: 26px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ftco-footer-social li a:hover {
  color: #fff;
}

.footer-small-nav > li {
  display: inline-block;
}

.footer-small-nav > li a {
  margin: 0 10px 10px 0;
}

.footer-small-nav > li a:hover, .footer-small-nav > li a:focus {
  color: #4586ff;
}

.media .ftco-icon {
  width: 100px;
}

.media .ftco-icon span {
  color: #4586ff;
}

.ftco-media {
  background: #fff;
  border-radius: 5px;
}

.ftco-media.text-center .ftco-icon {
  margin: 0 auto;
}

.ftco-overflow-hidden {
  overflow: hidden;
}

.padding-top-bottom {
  padding-top: 120px;
  padding-bottom: 120px;
}

.ftco-owl {
  z-index: 1;
  position: relative;
}

.ftco-owl .owl-nav {
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  -o-transition: .3s all ease;
  margin-top: -50px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
}

.ftco-owl .owl-nav .owl-prev, .ftco-owl .owl-nav .owl-next {
  color: #fff;
  background: #4586ff;
  padding: 10px;
  font-size: 30px;
  line-height: 0;
  position: absolute;
}

.ftco-owl .owl-nav .owl-prev {
  left: 0;
}

.ftco-owl .owl-nav .owl-next {
  right: 0;
}

.ftco-owl .owl-dots {
  text-align: center;
  margin-top: 30px;
  position: relative;
}

.ftco-owl .owl-dots .owl-dot {
  zoom: 1;
  display: inline-block;
}

.ftco-owl .owl-dots .owl-dot span {
  background: #ccc;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  margin: 5px 7px;
  display: inline-block;
}

.ftco-owl .owl-dots .owl-dot.active span {
  background: #4586ff;
}

.ftco-owl:hover .owl-nav {
  visibility: visible;
  opacity: 1;
}

@media screen and (width <= 768px) {
  #map {
    height: 400px;
  }
}

.ftco-counter .ftco-number {
  color: #4586ff;
  font-size: 72px;
  font-weight: bold;
  display: block;
}

.ftco-counter .ftco-label {
  text-transform: uppercase;
  letter-spacing: .1em;
  color: #b3b3b3;
  font-size: 12px;
}

#ftco-loader {
  opacity: 0;
  visibility: hidden;
  -o-transition: opacity .2s ease-out, visibility 0s linear .2s;
  z-index: 1000;
  background-color: #ffffffe6;
  border-radius: 16px;
  width: 96px;
  height: 96px;
  transition: opacity .2s ease-out, visibility 0s linear .2s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 24px 64px #0000003d;
}

#ftco-loader.fullscreen {
  -webkit-box-shadow: none;
  box-shadow: none;
  background-color: #fff;
  border-radius: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  top: 0;
  left: 0;
  transform: none;
}

#ftco-loader.show {
  -o-transition: opacity .4s ease-out, visibility 0s linear 0s;
  visibility: visible;
  opacity: 1;
  transition: opacity .4s ease-out, visibility linear;
}

#ftco-loader .circular {
  animation: 2s linear infinite loader-rotate;
  display: block;
  position: absolute;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
  transform: rotate(0);
}

#ftco-loader .path {
  stroke-dasharray: 1 200;
  stroke-dashoffset: 0;
  stroke-linecap: round;
  animation: 1.5s ease-in-out infinite loader-dash;
}

@keyframes loader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-dash {
  0% {
    stroke-dasharray: 1 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 89 200;
    stroke-dashoffset: -136px;
  }
}

